.store {
  display: flex;
  flex-direction: column;
  margin-top: 0;
}
.store-picture {
  width: 100%;
  height: auto;
}

.store__header {
  content: '';
  display: table;
  width: 100%;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.21;
  margin-bottom: 32px;
}
.line {
  display: table-cell;
  width: 100%;
  padding-left: 8px;
  border: none;
}
.line:after {
  content: '';
  border-top: 1px solid;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
}
.store-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin-bottom: 32px;
}
.store-list__item {
  width: calc(38% + 26px);
}
.store__product {
  text-align: left;
}
.store__product--name {
  display: flex;
  white-space: nowrap;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  margin-top: 12px;
  margin-bottom: 6px;
}
.store__item--price {
  opacity: 0.6;
  font-weight: 600;
  font-size: 12px;
}
.store__button {
  @include button-m;
  display: flex;
  margin: auto;
  padding: 8px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.33;
  align-items: center;
  justify-content: center;
  border: 0;
  letter-spacing: 0.06em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  color: $white;
  transition: $transition;
  &:hover,
  &:focus {
    background-color: $hover;
    color: $white;
    scale: 1.2;
  }
}
@include tablet-only {
  .store__header {
    content: '';
    display: table;
    width: 100%;
    font-size: 44px;
    line-height: 1.23;
    margin-bottom: 60px;
  }
  .line {
    display: table-cell;
    width: 100%;
    padding-left: 8px;
    border: none;
  }
  .line:after {
    content: '';
    border-top: 1px solid #000000;
    width: 100%;
    display: inline-block;
    vertical-align: middle;
  }
  .store-list {
    gap: 32px;
    margin-bottom: 60px;
  }
  .store-list__item {
    flex-basis: calc((100% - 32px) / 2);
  }
  .store__product--name {
    display: table;
    white-space: nowrap;
    font-size: 32px;
    line-height: 1.25;
    margin-top: 16px;
  }
  .store__product--price {
    font-size: 24px;
    line-height: 1.2;
    margin-bottom: 8px;
  }
  .store__button {
    @include button-l;
    font-size: 28px;
  }
}

@include desktop {
  .store {
    display: flex;
    flex-direction: column;
  }
  .store__header {
    font-weight: 700;
    font-size: 44px;
    line-height: 1.23;
    margin-bottom: 80px;
  }
  .store-list {
    gap: 32px;
  }
  .store__product {
    margin-bottom: 32px;
  }
  .store-list__item {
    flex-basis: calc((100% - 32px) / 2);
  }
  .store__product--name {
    display: table;
    white-space: nowrap;
    font-size: 36px;
    line-height: 1.2;
    margin-top: 16px;
    margin-bottom: 4px;
  }
  .line {
    display: table-cell;
    width: 100%;
    padding-left: 16px;
    border: none;
  }
  .line:after {
    content: '';
    border-top: 1px solid #000000;
    width: 100%;
    display: inline-block;
    vertical-align: middle;
  }
  .store__product--price {
    font-size: 28px;
    line-height: 1.2;
  }
  .store__button {
    @include button-l;
    font-size: 28px;
  }
}
