.header {
  flex-direction: row;
  align-items: center;
  margin: 0 auto;
  padding: 16px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header {
    max-width: 1280px;
    margin: 0 auto 100px;
    padding: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    margin: 0 auto -113px;
    padding: 28px 0 0;
  }
}

.header__logo {
  fill: #000;
  display: block;
}

@media screen and (min-width: 768px) {
  .header__logo {
    width: 72px;
    height: 57px;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header__logo:hover, .header__logo:focus {
    stroke: #e86f54;
    width: 79px;
    height: 64px;
    fill: #e86f54;
    margin: -3.5px;
  }
}

@media screen and (min-width: 1280px) {
  .header__logo {
    fill: #fff;
    z-index: 1;
    position: relative;
  }
}

@media screen and (min-width: 768px) {
  .header__phone {
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header__phone:hover, .header__phone:focus {
    height: 33px;
    width: 33px;
    margin: -2.5px;
    animation: 1.2s ease-in-out infinite trambling-animation;
  }

  @keyframes trambling-animation {
    0%, 50%, 100% {
      transform: rotate(0);
    }

    10%, 30% {
      transform: rotate(-10deg);
    }

    20%, 40% {
      transform: rotate(10deg);
    }
  }
}

.header__logo-link, .header__phone-link {
  cursor: pointer;
  display: block;
  position: relative;
}

.header__logo-link {
  margin-right: auto;
}

.header__phone-link {
  z-index: 998;
  margin-left: auto;
  margin-right: 17px;
  display: block;
}

@media screen and (min-width: 768px) {
  .header__phone-link {
    padding-right: 25px;
  }
}

@media screen and (min-width: 1280px) {
  .header__phone-link {
    padding-right: 28px;
  }
}

.header__phone {
  fill: #000;
  cursor: pointer;
  margin-right: 14px;
  display: block;
}

@media screen and (min-width: 768px) {
  .header__phone {
    width: 24px;
    height: 24px;
    margin-right: 0;
  }
}

@media screen and (min-width: 1280px) {
  .header__phone {
    width: 28px;
    height: 28px;
    z-index: 1;
    fill: #fff;
    position: relative;
  }
}

.header__burger {
  fill: #000;
  z-index: 998;
  display: block;
}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
