  height: 100vh;
  z-index: 1000;
  background-color: #e86f54;
  flex-direction: column;
  padding: 124px 40px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(200%);
