.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  visibility: visible;
  opacity: 1;
  background-color: #0003;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  z-index: 100;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

